<template>
<div class="list">
  <header>
    <h2>午茶時光</h2>
    <p>今天想點.... 
      <input type="text" v-model="newOrder" @keyup.enter="addOrder">
    </p>
  </header>
  <section>
    <ul class="flex-box tabs">
      <li @click="showTab('all')">
        <a href="javascript:void(0)">
          所有訂單 ({{ totalSum }})
        </a>
      </li>
      <li @click="showTab('nodo')">
        <a href="javascript:void(0)">
          未完成 ({{ nodoSum }})
        </a>
      </li>
      <li @click="showTab('done')">
        <a href="javascript:void(0)">
          已完成 ({{ doneSum }})
        </a>
      </li>
    </ul>
    <ul class="order-list">
      <li class="order-row">
        <div class="flex-box order-detail" v-for="(value, index) in list" :key="'order-' + index">
          <template v-if="value === editOrder">
            <input type="text" v-model="value.content" @keyup.enter="doneEdit(value)" @keyup.esc="cancelEdit(value)" @blur="cancelEdit(value)" v-input-focus>
          </template>
          <template v-else>
            <div class="order">
              <input type="checkbox" :class="['status', value.done ? 'done' : 'nodo']" :name="'order-' + index" :id="'order-' + index" v-model="value.done">
              <label class="order-content" :for="'order-' + index" @dblclick="edit(value)">
                {{ value.content }}
              </label>
            </div>
            <div class="btn-group">
              <button @click="edit(value)">修改</button>
              <button>棄單</button>
            </div>
          </template>
        </div>
      </li>
    </ul>
  </section>

</div>
</template>

<script>
export default {
  name: 'todoList',
  props: {
    msg: String
  },
  data() {
    return {
      whichTab: 'all',
      newOrder: '',
      orders: [{
        content: '綠茶',
        done: true
      }, {
        content: '奶茶',
        done: false
      },{
        content: '綠茶',
        done: true
      },{
        content: '綠茶',
        done: true
      }],
      editOrder: null,
      inputLock: false
    }
  },
  computed: {
    list: function() {
      switch (this.whichTab) {
        case 'all': 
          return this.orders;
        case 'nodo': 
          return this.orders.filter(value => {
            return !value.done;
          });
        case 'done': 
          return this.orders.filter(value => {
            return value.done
          });
      }
    },
    totalSum: function() {
      return this.orders.length;
    },
    nodoSum: function() {
      let nodo = this.orders.filter(value => {
        return !value.done
      });
      return nodo.length
    },
    doneSum: function() {
      let done = this.orders.filter(value => {
        return value.done;
      });
      return done.length
    }
  },
  methods: {
    showTab (value) {
      this.whichTab = value;
    },
    addOrder() {
        let value = this.newOrder && this.newOrder.trim();
        if(!value) {
            return;
        }
        this.orders.push({ content: value, done: false });
        this.newOrder = '';
    },
    edit(value) {
      this.editOrder = value;
    },
    doneEdit(value) {
      if(!this.editOrder) return;
      this.editOrder = null;
      value.content = value.content.trim();
      if(!value.content){
          this.removeData(value)
      }
      this.inputLock = true;
    },
    cancelEdit(value) {
      if (!this.inputLock) {
        this.editOrder = null;
      }
      this.inputLock = false;
    },
    removeOrder: function(item){
        var index = this.orders.indexOf(item);
        this.orders.splice(index, 1);
    }, 
  },
  directives :{
        inputFocus :{
            inserted: function(el){
                el.focus();
            }
        }
    }
}
</script>
